body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  font-size: 62.5%;
}

body {
  background-color: #0d1d25;
}

.App {
  overflow-x: hidden;
  position: relative;
  margin: auto;
  min-height: 800px;
  width: 80%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App .clock {
  margin: 6rem auto;
  margin-top: 2rem;
  width: 90%;
  height: 310px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 4px solid #0d1d25;
  box-shadow: 0 -25px 25px #ffffff05, inset 0 -25px 25px #ffffff05, 0 5px 25px #000000, inset 0 25px 25px #000000;
}

.App .clock .time {
  font-size: 15rem;
  color: white;
}

.App .injection {
  margin: 1.8rem;
  width: 90%;
  display: flex;
  font-family: sans-serif;
  font-size: 6.5rem;
  font-weight: 600;
  letter-spacing: 0.5rem;
  text-align: center;
  background-color: #4396ba;
  border: 2px solid #000000;
  box-shadow: 0 10px 15px #000000;
  cursor: pointer;
  transform: all 2s;
}

.App .injection .injection-description {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.App .injection .injection-description p {
  margin: 3rem;
}

.App .injected {
  background-color: #13891c;
  transform: all 2s;
}

.App .highlighted {
  background-color: #7b2b2f;
  -webkit-animation: glow 4s infinite;
          animation: glow 4s infinite;
  transform: all 2s;
}

@-webkit-keyframes glow {
  0% {
    box-shadow: 0 0 10px 5px #a41920;
    transform: scale(1);
  }
  50% {
    box-shadow: 0 0 10px 10px #a41920;
    transform: scale(1.04);
  }
  100% {
    box-shadow: 0 0 10px 5px #a41920;
    transform: scale(1);
  }
}

@keyframes glow {
  0% {
    box-shadow: 0 0 10px 5px #a41920;
    transform: scale(1);
  }
  50% {
    box-shadow: 0 0 10px 10px #a41920;
    transform: scale(1.04);
  }
  100% {
    box-shadow: 0 0 10px 5px #a41920;
    transform: scale(1);
  }
}

@media screen and (max-width: 1200px) {
  .App {
    width: 90%;
  }
  .App .clock {
    height: 250px;
  }
  html {
    font-size: 8px;
  }
}

@media screen and (max-width: 720px) {
  .App {
    width: 90%;
  }
  .App .clock {
    height: 200px;
  }
  html {
    font-size: 6px;
  }
}

@media screen and (max-width: 540px) {
  .App {
    width: 90%;
  }
  .App .clock {
    height: 150px;
  }
  html {
    font-size: 4px;
  }
}
